export default function set(store, path, value) {
  let t = store.value;
  let p = Array.isArray(path) ? path.join(".").split(".") : path.split(".");

  for (var i = 0, n = p.length - 1; i < n; i++) {
    if (typeof t[p[i]] !== "object" || t[p[i]] == null) {
      t[p[i]] = {};
    } else if (Array.isArray(t[p[i]])) {
      t[p[i]] = t[p[i]].slice();
    } else if (typeof t[p[i]] === "object") {
      t[p[i]] = Object.assign({}, t[p[i]]);
    }

    t = t[p[i]];
  }

  t[p.slice(-1)[0]] = value;
}


